function readData() {


    //  scene.add(particles);

    // const pointLight = new THREE.PointLight( 0xffffff, 0.8 );
    const loader = new THREE.ImageBitmapLoader();



    groupBillboardTexture = new THREE.Group();
    groupBillboardWhite = new THREE.Group();

    for (const item of jsonBillboardInfo.information.billboards) {
        console.log(item)


//Defines the size
let w = 1;
if(Hololens == true)
{
    w = 0.6;
}

        // set options if needed
        loader.setOptions({ imageOrientation: 'flipY' });

        // load a image resource
        loader.load(
            // resource URL
            item.src,
            // onLoad callback
            function (imageBitmap) {
                let texture = new THREE.CanvasTexture(imageBitmap);
                let materialTexture = new THREE.MeshBasicMaterial({ map: texture, side: THREE.DoubleSide });
               // let geometry = new THREE.PlaneGeometry(item.width, item.height);
                let geometry = new THREE.PlaneGeometry(w, w);
                geometry.lookAt(new Vector3(item.rotation.x, item.rotation.y, item.rotation.z));  //Rotation
                let planeTexture = new THREE.Mesh(geometry, materialTexture);
                planeTexture.position.x = item.position.x;   //Position
                planeTexture.position.y = item.position.y;
                planeTexture.position.z = item.position.z;

                groupBillboardTexture.add(planeTexture);
            },

            // onProgress callback currently not supported
            undefined,
            // onError callback
            function (err) {
                console.log('An error happened');
            }
        );


        // load a image resource
        loader.load(
            // resource URL
            item.src1,
            // onLoad callback
            function (imageBitmap) {
                let texture = new THREE.CanvasTexture(imageBitmap);
                let materialTexture = new THREE.MeshBasicMaterial({ map: texture, side: THREE.DoubleSide });
                //let materialWhite = new THREE.MeshBasicMaterial({ color: 0xffffff, side: THREE.DoubleSide });
                let geometry = new THREE.PlaneGeometry(item.width, item.height);
                geometry.lookAt(new Vector3(item.rotation.x, item.rotation.y, item.rotation.z));  //Rotation
                let planeTexture = new THREE.Mesh(geometry, materialTexture);
                planeTexture.position.x = item.position.x;   //Position
                planeTexture.position.y = item.position.y;
                planeTexture.position.z = item.position.z;

                let planeWhite = new THREE.Mesh(geometry, materialTexture);
                planeWhite.position.x = item.position.x;   //Position
                planeWhite.position.y = item.position.y;
                planeWhite.position.z = item.position.z;

                groupBillboardWhite.add(planeWhite);
            },

            // onProgress callback currently not supported
            undefined,
            // onError callback
            function (err) {
                console.log('An error happened');
            }
        );


    }

    scene.add(groupBillboardTexture);
    scene.add(groupBillboardWhite);

    //By deflaut show the texture billboard
    groupBillboardTexture.visible = true;
    groupBillboardTexture.visible = false;
}


