function onSelectStart1(event) {
    console.log('Entering  onSelectStart');

    state = 'grab1'; //Hololens 2 Version
 
    //Show the guide line
    if (Hololens == false) {
        groupVisible.attach(guideline);
    }
    else {

        //Hololens mode

        //Create a sound
        beep();

        //Toogle the view
        if (groupBillboardTexture.visible == true) {
            groupBillboardTexture.visible = false;
            groupBillboardWhite.visible = true;
        }
        else {
            groupBillboardTexture.visible = true;
            groupBillboardWhite.visible = false;
        }

        
        //Get the inital controller positions
        hololensGrabController1 = controller1.getWorldPosition(new THREE.Vector3(0, 0, 0));

        let xrCamera = renderer.xr.getCamera(camera);
        // hololens1Grab3UserPosition =  xrCamera.getWorldPosition(new THREE.Vector3(0, 0, 0));
        // hololens1Grab3Forward =  xrCamera.getWorldDirection(new THREE.Vector3(0, 0, 1));

        if (toggleAttachedObj == true) {
            scene.attach(obj);
            toggleAttachedObj = false;
        }
        else {
            oldCameraXRPos.copy(camera.position);
            // camera.getWorldPosition( oldCameraXRPos );
            oldCameraXRRot.copy(camera.rotation);
            objPivot = new THREE.Group();
            camera.getWorldPosition(objPivot.position); //.copy(oldCameraXRPos);
            scene.add(objPivot);
            objPivot.attach(obj);
            toggleAttachedObj = true;

            objPivot.add(boxPivot);
        }

    }
}

