function onSelectEnd1(event) {
    console.log('Entering  onSelectEnd');

    if (Hololens == false) {
        //  console.log( 'Which is this:' +  obj.constructor.name );

        // Hide the guide line
        groupHidden.attach(guideline);

        state = 'none';

        //    if ( guidingController === this) {

        console.log(' guidingController === this' + this);

        //Jump to the new position
        // feet position, which is the head position but on the ground
        const feetPos = renderer.xr
            .getCamera(camera)
            .getWorldPosition(tempVec);
        feetPos.y = 0;

        // cursor position, same logic as earlier
        let p = controller1.getWorldPosition(tempVecP);
        let v = controller1.getWorldDirection(tempVecV);

        v.multiplyScalar(6);
        const t = (-v.y + Math.sqrt(v.y ** 2 - 2 * p.y * g.y)) / g.y;
        const cursorPos = positionAtT(tempVec1, t, p, v, g);
        // Offset
        const offset = cursorPos.addScaledVector(feetPos, -1);


        dolly.position.x += offset.x;
        dolly.position.y += offset.y;
        dolly.position.z += offset.z;

    }
    else {
        //Hololens version
        if (state == 'state3') {
            scene.attach(obj);
        }


        state = 'none';

    }
    //   }
}



function onSelectEnd2(event) {
    console.log('Entering  onSelectEnd');

    if (Hololens == false) {
        //Dekstop and VR version

        // Hide the guide line
        groupHidden.attach(guideline);

        state = 'none';


        console.log(' guidingController === this' + this);

        //Jump to the new position
        // feet position, which is the head position but on the ground
        const feetPos = renderer.xr
            .getCamera(camera)
            .getWorldPosition(tempVec);
        feetPos.y = 0;

        // cursor position, same logic as earlier
        const p = controller2.getWorldPosition(tempVecP);
        const v = controller2.getWorldDirection(tempVecV);
        v.multiplyScalar(6);
        const t = (-v.y + Math.sqrt(v.y ** 2 - 2 * p.y * g.y)) / g.y;
        const cursorPos = positionAtT(tempVec1, t, p, v, g);
        // Offset
        const offset = cursorPos.addScaledVector(feetPos, -1);


        dolly.position.x += offset.x;
        dolly.position.y += offset.y;
        dolly.position.z += offset.z;

    }
    else {
        //Hololens version
        state = 'none';

    }

}

function positionAtT(inVec, t, p, v, g) {
    inVec.copy(p);
    inVec.addScaledVector(v, t);
    inVec.addScaledVector(g, 0.5 * t ** 2);
    return inVec;
}



