function AnimationEffect() {
    renderer.setAnimationLoop(render);
}


let oldCameraXRPos = new THREE.Vector3();
let oldCameraXRRot = new THREE.Vector3();

var rotWorldMatrix;
// Rotate an object around an arbitrary axis in world space       
function rotateAroundWorldAxis(object, axis, radians) {
    rotWorldMatrix = new THREE.Matrix4();
    rotWorldMatrix.makeRotationAxis(axis.normalize(), radians);

    // old code for Three.JS pre r54:
    //  rotWorldMatrix.multiply(object.matrix);
    // new code for Three.JS r55+:
    rotWorldMatrix.multiply(object.matrix);                // pre-multiply

    object.matrix = rotWorldMatrix;

    // old code for Three.js pre r49:
    // object.rotation.getRotationFromMatrix(object.matrix, object.scale);
    // old code for Three.js pre r59:
    // object.rotation.setEulerFromRotationMatrix(object.matrix);
    // code for r59+:
    object.rotation.setFromRotationMatrix(object.matrix);
}


function render() {

    //Interaction with the button
    //  handleCollisions();


    //add gamepad polling for webxr to renderloop
    dollyMove();


    //Stat machine none -> store the current controler location to later compute the delta
    if (state === 'none') {
        refPosition = {
            x: controller1.position.x,
            y: controller1.position.y,
            z: controller1.position.z
        };
    }


    if (state === 'grab1' || state === 'grab2') {

        if (Hololens == false) {
            //Update the guide line
            //Update the line direciton
            // Controller start position
            let p = controller1.getWorldPosition(tempVecP);
            // Set Vector V to the direction of the controller, at 1m/s
            let v = controller1.getWorldDirection(tempVecV);

            if (state === 'grab2') {
                p = controller2.getWorldPosition(tempVecP);
                // Set Vector V to the direction of the controller, at 1m/s
                v = controller2.getWorldDirection(tempVecV);
            }
            // Scale the initial velocity to 6m/s
            v.multiplyScalar(6);
            // Calculate t, this is the above equation written as JS
            const t = (-v.y + Math.sqrt(v.y ** 2 - 2 * p.y * g.y)) / g.y;

            const vertex = tempVec.set(0, 0, 0);
            for (let i = 0; i <= lineSegments; i++) {
                // set vertex to current position of the virtual ball at time t
                positionAtT(vertex, i * t / lineSegments, p, v, g);
                // Copy it to the Array Buffer
                vertex.toArray(lineGeometryVertices, i * 3);
                //     console.log( 'Point x: ' + vertex.x + 'y'+ vertex.y + 'z'+ vertex.z );

            }

            guideline.geometry.attributes.position.needsUpdate = true;

        }
        else {
            //Hololens mode !!!!!!

            //Does not work
            // if (state == 'grap3')
            // {

            //     let xrCamera = renderer.xr.getCamera(camera);
            //     const hololens1Grab3UserPositionN =  xrCamera.getWorldPosition(new THREE.Vector3(0, 0, 0));
            //     const hololens1Grab3ForwardN =  xrCamera.getWorldDirection(new THREE.Vector3(0, 0, 1));


            //         //Hololens 1 version with one hand grab
            //         const deltaX = hololens1Grab3UserPosition.x - hololens1Grab3UserPositionN.x;
            //         const deltaY = hololens1Grab3UserPosition.y - hololens1Grab3UserPositionN.y;
            //         const deltaZ = hololens1Grab3UserPosition.z - hololens1Grab3UserPositionN.z;

            //         //Move the view
            //         dolly.lookAt(new Vector3(dolly.position.x + deltaX, dolly.position.y +deltaY, dolly.position.z + deltaZ));
            //         hololens1Grab3UserPosition = hololens1Grab3UserPositionN;

            // }


            if (state === 'grab1') {
                //Rotate the view
                const p = controller1.getWorldPosition(new THREE.Vector3(0, 0, 0));
                const delta = hololensGrabController1.y - p.y;
                hololensGrabController1 = p;

                //Rotate the view
                var axis = new THREE.Vector3(0, 1, 0);
                forward.applyAxisAngle(axis, delta);
                dolly.lookAt(new Vector3(dolly.position.x + forward.x, dolly.position.y + forward.y, dolly.position.z + forward.z));

            }

            if (state === 'grab2') {
                //Hololens 2 version
                //Use the second hand to move the scene
                //Change the altitude and the location
                const p = controller2.getWorldPosition(new THREE.Vector3(0, 0, 0));
                const r = 1;
                const deltaX = hololensGrabController2.x - p.x;
                const deltaY = hololensGrabController2.y - p.y;
                const deltaZ = hololensGrabController2.z - p.z;
                hololensGrabController2 = p;

                //Shif the view 
                dolly.position.set(dolly.position.x + deltaX * r, dolly.position.y + deltaY* r, dolly.position.z + deltaZ* r);

            }

        }
    }

    //render the scene
    //  renderer.render( scene,  camera);


    var endDate = new Date();
    secondsSpan = (endDate.getTime() - lastRenderTime.getTime()) / 1000;
    lastRenderTime = new Date();

    //Gestion du key down and up
    var angle = 4 * Math.PI / 6 * secondsSpan;  //rotation to be applied for the given timespan

    if (MouseDown == true) {
        dolly.lookAt(new Vector3(dolly.position.x + forward.x, dolly.position.y + forward.y, dolly.position.z + forward.z));
    }

    if (LeftKeyPressed == true) {
        //Left rotation
        var axis = new THREE.Vector3(0, 1, 0);
        forward.applyAxisAngle(axis, angle);

        dolly.lookAt(new Vector3(dolly.position.x + forward.x, dolly.position.y + forward.y, dolly.position.z + forward.z));
    }

    if (RightKeyPressed == true) {
        //Right rotation
        var axis = new THREE.Vector3(0, 1, 0);
        forward.applyAxisAngle(axis, -angle);

        dolly.lookAt(new Vector3(dolly.position.x + forward.x, dolly.position.y + forward.y, dolly.position.z + forward.z));
    }

    if (BackwarddKeyPressed == true) {
        dolly.position.x += secondsSpan * speed * forward.x;
        dolly.position.y += secondsSpan * speed * forward.y;
        dolly.position.z += secondsSpan * speed * forward.z;

       dolly.lookAt(new Vector3(dolly.position.x + forward.x, dolly.position.y + forward.y, dolly.position.z + forward.z));
    }

    if (ForwardKeyPressed == true) {
        //  dolly.position.set(dolly.position.x - secondsSpan * speed * forward.x, dolly.position.y - secondsSpan * speed * forward.y, dolly.position.z - secondsSpan * speed * forward.z);

        dolly.position.x -= secondsSpan * speed * forward.x;
        dolly.position.y -= secondsSpan * speed * forward.y;
        dolly.position.z -= secondsSpan * speed * forward.z;

        dolly.lookAt(new Vector3(dolly.position.x + forward.x, dolly.position.y + forward.y, dolly.position.z + forward.z));
    }


    //If the grap 3 is on
    if (toggleAttachedObj == true) {

        //What is this?????

        // objPivot.rotation.y -= oldCameraXRRot.y - camera.rotation.y;

        // const delta = camera.getWorldPosition(camera.position) - objPivot.getWorldPosition(objPivot.position);

        // objPivot.position.add(delta);
        // const camPos = camera.position;
        // console.log("Camera Position " + camPos);
        // camPos = camera.getWorldPosition(camera.position)
        // console.log("Camera Position world " + camPos);

        // console.log("Delta " + delta);

        // rotateAroundWorldAxis(objPivot, new THREE.Vector3(0, 1, 0), oldCameraXRRot.y - camera.rotation.y);
        // objPivot.position.sub(delta);

        // objPivot.position.sub(oldCameraXRPos.sub(camera.position));

        // oldCameraXRPos.copy(camera.position);

        // //Apply the rotation but around user location
        // oldCameraXRRot.copy(camera.rotation);

    }

    renderer.render(scene, camera);
}







let percentageLoaded;

function loadOBJ(modelName, scene) {
    console.log("Enter OBJ Loader ");

    // crée un nouvel élément div
    //var newDiv = document.createElement("div");
    // et lui donne un peu de contenu
    percentageLoaded = document.createTextNode('Hello from Christophe');
    // ajoute le nœud texte au nouveau div créé
    container.appendChild(percentageLoaded);

    // ajoute le nouvel élément créé et son contenu dans le DOM
    //   var currentDiv = document.getElementById('div');
    //   document.body.insertAdjacentText(newDiv, currentDiv);



    const onProgress = function (xhr) {

        if (xhr.lengthComputable) {

            const percentComplete = xhr.loaded / xhr.total * 100;
            console.log(Math.round(percentComplete, 2) + '% downloaded');
            percentageLoaded.textContent = Math.round(percentComplete, 2) + '% downloaded';
        }

    };

    const onError = function (err) { console.log("Error Loading obj : " + err); };

    const manager = new THREE.LoadingManager();
    manager.addHandler(/\.dds$/i, new DDSLoader());

    // comment in the following line and import TGALoader if your asset uses TGA textures
    // manager.addHandler( /\.tga$/i, new TGALoader() );

    new MTLLoader(manager)
        //   .setPath( 'models/obj/christophe/' )
        .load(modelName + '.mtl', function (materials) {
            materials.preload();
            new OBJLoader(manager)
                .setMaterials(materials)
                //    .setPath( 'models/obj/christophe/' )
                .load(modelName + '.obj', function (object) {
                    //   object.scale.setScalar(1);
                    //   object.position.x = -1;
                    //   object.position.y = 3;
                    // scene.add( object );
                    //   group.add( object );
                    //    objloaded = object;
                    obj = object;
                    //   group.add( object );
                    scene.add(obj);
                    console.log("Obj Loaded");

                    // //remove the percentage loader
                    percentageLoaded.remove();
                    container.appendChild(renderer.domElement);

                }, onProgress, onError);

        });



}

function dollyMove(event) {

    var handedness = "unknown";

    var hasLeftRightDown = false;  //Trigger to detect the thumbstick up



    //determine if we are in an xr session
    const session = renderer.xr.getSession();
    let i = 0;

    if (session) {
        let xrCamera = renderer.xr.getCamera(camera);
        xrCamera.getWorldDirection(cameraVector);

        //a check to prevent console errors if only one input source
        if (isIterable(session.inputSources)) {
            for (const source of session.inputSources) {
                if (source && source.handedness) {
                    handedness = source.handedness; //left or right controllers
                }
                if (!source.gamepad) continue;
                const controller = renderer.xr.getController(i++);
                const old = prevGamePads.get(source);
                const data = {
                    handedness: handedness,
                    buttons: source.gamepad.buttons.map((b) => b.value),
                    axes: source.gamepad.axes.slice(0)
                };


                if (old) {
                    data.buttons.forEach((value, i) => {
                        //handlers for buttons
                        if (value !== old.buttons[i] || Math.abs(value) > 0.8) {
                            //check if it is 'all the way pushed'
                            if (value === 1) {
                                console.log("Button" + i + "Down");
                                if (data.handedness == "left") {
                                    console.log("Left Paddle Down");
                                    if (i == 1) {
                                        //Do the incrementatl rotation every 500 ms
                                        dolly.rotateY(-THREE.Math.degToRad(1));
                                    }
                                    if (i == 3) {
                                        //reset teleport to home position
                                        dolly.position.x = 0;
                                        dolly.position.y = 5;
                                        dolly.position.z = 0;
                                    }
                                } else {
                                    if (i == 1) {
                                        console.log("Right Paddle Down");

                                        if (state === "Grab") {
                                            //Move the floor with the correct rotation


                                            const delta = new THREE.Vector3(
                                                refPosition.x - controller1.position.x,
                                                refPosition.y - controller1.position.y,
                                                refPosition.z - controller1.position.z);

                                            //    delta.applyAxisAngle(new THREE.Vector3(0,1,0),  dolly.rotation.y);

                                            delta.applyQuaternion(dolly.quaternion);
                                            //   delta.update();

                                            //      console.log("Rotation : " +  dolly.rotation.y);
                                            //      console.log("deltaY : " +  dolly.rotation.y);

                                            dolly.position.x += delta.x;
                                            dolly.position.y += delta.y;
                                            dolly.position.z += delta.z;
                                        }
                                        //Grap the floor
                                        refPosition = {
                                            x: controller1.position.x,
                                            y: controller1.position.y,
                                            z: controller1.position.z
                                        };
                                        state = "Grab";
                                        //trigger the state
                                    }



                                }
                            } else {
                                console.log("Button" + i + "Up");

                                if (i == 1) {
                                    //use the paddle buttons to rotate
                                    if (data.handedness == "left") {
                                        //console.log("Left Paddle Down");
                                        //    dolly.rotateY(-THREE.Math.degToRad(Math.abs(value)));
                                    } else {
                                        console.log("Right Paddle Down");
                                        //  dolly.rotateY(THREE.Math.degToRad(Math.abs(value)));

                                        //Move the dolly
                                        state = "stopGrab";
                                    }
                                }


                                if (i == 2) {
                                    //    console.log("Right button 2  Down");

                                    if (data.handedness == "left") {

                                        //Toogle data vis of not
                                        if (groupBillboardTexture.visible == true) {
                                            groupBillboardTexture.visible = false;
                                            groupBillboardWhite.visible = true;
                                        }
                                        else {
                                            groupBillboardTexture.visible = true;
                                            groupBillboardWhite.visible = false;
                                        }

                                        //console.log("Left Paddle Down");
                                        //    dolly.rotateY(-THREE.Math.degToRad(Math.abs(value)));
                                    } else {

                                        //Do a user back to the center of the experimentation
                                        console.log('center back the view');
                                        dolly.position.set(jsonBillboardInfo.information.initialposition.x, jsonBillboardInfo.information.initialposition.y, jsonBillboardInfo.information.initialposition.z);

                                    }

                                }



                            }
                        }
                    });




                    data.axes.forEach((value, i) => {
                        //handlers for thumbsticks
                        //if thumbstick axis has moved beyond the minimum threshold from center, windows mixed reality seems to wander up to about .17 with no input
                        if (Math.abs(value) > 0.2) {

                            if (i == 2) {
                                //left and right axis on thumbsticks
                                if (data.handedness == "left") {
                                    (data.axes[2] > 0) ? console.log('left on left thumbstick') : console.log('right on left thumbstick')

                                
                                    //provide haptic feedback if available in browser
                                    if (
                                        source.gamepad.hapticActuators &&
                                        source.gamepad.hapticActuators[0]
                                    ) {
                                        var pulseStrength = Math.abs(data.axes[2]) + Math.abs(data.axes[3]);
                                        if (pulseStrength > 0.75) {
                                            pulseStrength = 0.75;
                                        }

                                        var didPulse = source.gamepad.hapticActuators[0].pulse(
                                            pulseStrength,
                                            100
                                        );
                                    }



                                } else {

                                    //Do the rotation only if large mouvement
                                    if ( Math.abs(data.axes[2]) > 0.9) {
                                        hasLeftRightDown = true;

                                        //  console.log('rotationTof ' + TimerRotation) ;

                                        //Do the incremtal rotation every 300 ms
                                        if (TimerRotation == 0 || TimerRotation > 0.3) {
                                            dolly.rotateY(-THREE.Math.degToRad(15) * Math.sign(data.axes[2]));
                                            TimerRotation = 0.01;
                                        } else {
                                            TimerRotation += secondsSpan;
                                        }
                                    }
                                }
                              
                            }



                            if (i == 3) {
                                //up and down axis on thumbsticks
                                if (data.handedness == "left") {
                                    (data.axes[3] > 0) ? console.log('up on left thumbstick') : console.log('down on left thumbstick')
                                    dolly.position.y -= speed * secondsSpan * data.axes[3]/3.0; //Slow up and down
                                    //provide haptic feedback if available in browser
                                    if (
                                        source.gamepad.hapticActuators &&
                                        source.gamepad.hapticActuators[0]
                                    ) {
                                        var pulseStrength = Math.abs(data.axes[3]);
                                        if (pulseStrength > 0.75) {
                                            pulseStrength = 0.75;
                                        }
                                        var didPulse = source.gamepad.hapticActuators[0].pulse(
                                            pulseStrength,
                                            100
                                        );
                                    }
                                } else {
                                    // (data.axes[3] > 0) ? console.log('up on right thumbstick') : console.log('down on right thumbstick')

                                    //Do the forward and backward only if big mouvement
                                    if (BlockWandForwardBackward == false && Math.abs(data.axes[3]) > 0.9) {                                        
                                        
                                        dolly.position.x -= cameraVector.x * speed * secondsSpan * data.axes[3]/3.0;  //Slow mouvement
                                        dolly.position.z -= cameraVector.z * speed * secondsSpan * data.axes[3]/3.0;  //Slow mouvement
                                      }


                                    //provide haptic feedback if available in browser
                                    if (
                                        source.gamepad.hapticActuators &&
                                        source.gamepad.hapticActuators[0]
                                    ) {
                                        var pulseStrength = Math.abs(data.axes[2]) + Math.abs(data.axes[3]);
                                        if (pulseStrength > 0.75) {
                                            pulseStrength = 0.75;
                                        }
                                        var didPulse = source.gamepad.hapticActuators[0].pulse(
                                            pulseStrength,
                                            100
                                        );
                                    }
                                }

                            }
                        } else {
                           
                        }
                    });
                }
                ///store this frames data to compate with in the next frame
                prevGamePads.set(source, data);
            }
        }
    }


    if (hasLeftRightDown == false) {
        TimerRotation = 0;// Set ot zero to insure when the user toto the left or right, it turns right away
    }



}

function isIterable(obj) {  //function to check if object is iterable
    // checks for null and undefined
    if (obj == null) {
        return false;
    }
    return typeof obj[Symbol.iterator] === "function";




}


let previousStateHitBox = false;
let previousStateHitBox2 = false;
let previousStateHitBox3 = false;

function handleCollisions() {

    const controllerBox = new THREE.Box3();
    controllerBox.setFromObject(selectorBox);

    let boxHit = new THREE.Box3();

    boxHit.setFromObject(box);
    if (boxHit.intersectsBox(controllerBox)) {

        console.log('hit');
        if (previousStateHitBox != true) {
            if (groupBillboardTexture.visible == true) {
                groupBillboardTexture.visible = false;
                groupBillboardWhite.visible = true;
            }
            else {
                groupBillboardTexture.visible = true;
                groupBillboardWhite.visible = false;
            }

            previousStateHitBox = true;
        }


    } else {
        previousStateHitBox = false;
    }



    boxHit.setFromObject(box2);
    if (boxHit.intersectsBox(controllerBox)) {

        console.log('hit2');
        if (previousStateHitBox2 != true) {

            //Place at the reference point
            dolly.position.set(jsonBillboardInfo.information.initialposition.x, jsonBillboardInfo.information.initialposition.y, jsonBillboardInfo.information.initialposition.z);
            forward = new Vector3(jsonBillboardInfo.information.forward.x, jsonBillboardInfo.information.forward.y, jsonBillboardInfo.information.forward.z);


            previousStateHitBox2 = true;
        }


    } else {
        previousStateHitBox2 = false;
    }




    boxHit.setFromObject(box3);
    if (boxHit.intersectsBox(controllerBox)) {

        console.log('hit3');
        if (previousStateHitBox3 != true) {


            if (obj.visible == true) {
                obj.visible = false;

                // obj.children.material = new THREE.MeshBasicMaterial();
                // obj.children.material.needsUpdate;
                // console.log(obj);
            }
            else {
                obj.visible = true;
            }


            previousStateHitBox3 = true;
        }


    } else {
        previousStateHitBox3 = false;
    }



}


