import { XRControllerModelFactory } from 'three/examples/jsm/webxr/XRControllerModelFactory.js';

export class WMRControllersManager {

    #prevGamePads;
    #renderer;

    constructor( renderer ) {

        this.initializedLeftRight = false;

        this.#prevGamePads = new Map();
        this.#renderer = renderer;

        this.verbose = 0;

        this.controller1 = renderer.xr.getController( 0 );
        this.controller2 = renderer.xr.getController( 1 );

        const controllerModelFactory = new XRControllerModelFactory();
        this.controllerGrip1 = renderer.xr.getControllerGrip( 0 );
        this.controllerGrip1.add( controllerModelFactory.createControllerModel( this.controllerGrip1 ) );
        this.controllerGrip2 = renderer.xr.getControllerGrip( 1 );
        this.controllerGrip2.add( controllerModelFactory.createControllerModel( this.controllerGrip2 ) );

    }

    getRightController() {

        if ( this.controller1.handedness === 'right' ) {

            return this.controller1;

        } else {

            return this.controller2;

        }

    }
    
    getRightControllerGrip() {

        if ( this.controller1.handedness === 'right' ) {

            return this.controllerGrip1;

        } else {

            return this.controllerGrip2;

        }

    }

    getLeftController() {

        if ( this.controller1.handedness === 'left' ) {

            return this.controller1;

        } else {

            return this.controller2;

        }

    }

    getLeftControllerGrip() {

        if ( this.controller1.handedness === 'left') {

            return this.controllerGrip1;

        } else {

            return this.controllerGrip2;

        }

    }

    update() {

        var handedness = "unknown";

        const session = this.#renderer.xr.getSession();
        let i = 0;

        if ( session ) {

            for ( const source of session.inputSources ) {

                if ( source && source.handedness ) {

                    handedness = source.handedness;

                }

                if ( !source.gamepad ) continue;

                const controller = this.#renderer.xr.getController( i++ );
                controller.handedness = handedness;
                const old = this.#prevGamePads.get( source );
                const data = {
                    handedness: handedness,
                    buttons: source.gamepad.buttons.map( ( b ) => b.value ),
                    axes: source.gamepad.axes.slice( 0 )
                };

                this.initializedLeftRight = true;

                if ( old ) {

                    data.buttons.forEach( ( value, i ) => {

                        if ( value !== old.buttons[ i ] ) {

                            switch ( i ) {

                                case 0: {

                                    // Select
                                    // if ( this.verbose ) {

                                    //     console.log( "Controller " + controller.handedness + " : " + 'selectPressed' );
                                    //     console.log( 'Value : ' + value );

                                    // }
                                    
                                    // source.dispatchEvent( { type: 'selectPressed', value: value } );

                                    // if ( value === 0 ) {

                                    //     if ( this.verbose ) {

                                    //         console.log( "Controller " + controller.handedness + " : " + 'selectend' );
    
                                    //     }

                                    //     source.dispatchEvent( { type: 'selectend' } );

                                    // } else if ( value === 1 ) {


                                    //     if ( this.verbose ) {

                                    //         console.log( "Controller " + controller.handedness + " : " + 'selectstart' );

                                    //     }

                                    //     source.dispatchEvent( { type: 'selectstart' } );

                                    // }

                                    break;

                                }

                                case 1: {

                                    // Grasp
                                    if ( this.verbose ) {

                                        console.log( "Controller " + controller.handedness + " : " + ( value ? 'graspdown' : 'graspup' ) );

                                    }

                                    controller.dispatchEvent( { type: value ? 'graspdown' : 'graspup' } );
                                    break;

                                }

                                case 2: {

                                    // Touchpad
                                    if ( this.verbose ) {

                                        console.log( "Controller " + controller.handedness + " : " + ( value ? 'touchpaddown' : 'touchpadup' ) );
                                        console.log( "x : " + data.axes[ 0 ] + ", y : " + data.axes[ 1 ] );

                                    }

                                    controller.dispatchEvent( { type: value ? 'touchpaddown' : 'touchpadup', axes: { x: data.axes[ 0 ], y: data.axes[ 1 ] } } );
                                    break;

                                }

                                case 3: {

                                    // Thumbstick
                                    if ( this.verbose ) {

                                        console.log( "Controller " + controller.handedness + " : " + ( value ? 'thumbstickDown' : 'thumbstickUp' ) );
                                        console.log( "x : " + data.axes[ 2 ] + ", y : " + data.axes[ 3 ] );

                                    }

                                    controller.dispatchEvent( { type: value ? 'thumbstickDown' : 'thumbstickUp', axes: { x: data.axes[ 2 ], y: data.axes[ 3 ] } } );
                                    break;

                                }

                                default: {

                                    break;

                                }

                            }

                        }


                    } );

                    if ( data.axes[ 0 ] !== old.axes[ 0 ] || data.axes[ 1 ] !== old.axes[ 1 ] ) {

                        // Touchpad Axes
                        if ( this.verbose ) {

                            console.log( "Controller " + controller.handedness + " : " + 'touchpadaxischanged' );
                            console.log( "x : " + data.axes[ 0 ] + ", y : " + data.axes[ 1 ] );

                        }
                        
                        controller.dispatchEvent( { type: 'touchpadaxischanged', axes: { x: data.axes[ 0 ], y: data.axes[ 1 ] } } )

                    }

                    if ( data.axes[ 2 ] !== old.axes[ 2 ] || data.axes[ 3 ] !== old.axes[ 3 ] ) {

                        // Thumbstick Axes
                        if ( this.verbose ) {

                            console.log( "Controller " + controller.handedness + " : " + 'axischanged' );
                            console.log( "x : " + data.axes[ 2 ] + ", y : " + data.axes[ 3 ] );

                        }
                        
                        controller.dispatchEvent( { type: 'axischanged', axes: { x: data.axes[ 2 ], y: data.axes[ 3 ] } } );

                    }

                }

                this.#prevGamePads.set( source, data );

            }

        }

    }

}